import React from "react";
import { getPokemon } from "./utils.js";
import PokeList from "./PokeList.js";
import List from "./List.js";
import PageButton from "./PageButton.js";
import DisplayPokemon from "./DisplayPokemon.js";
import { CSSTransition } from "react-transition-group";

class Pokemon extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			Pokemon: {
				pages: 1,
				currentPage: 1,
				data: null,
				nextPokemon: null,
				prevPokemon: null
			},
			charOn: false,
			choosedPokemon: false
		};
	}

	HelloPokemon(json, i) {
		let pagine = Math.round(json.count / 20);
		//console.log("le pagine sono:" + pagine);
		i = i ? i : 1;

		this.setState({
			Pokemon: {
				pages: pagine,
				currentPage: i,
				nextPokemon: json.next,
				prevPokemon: json.previous,
				data: json.results
			},
			charOn: false,
			choosedPokemon: false
		});
	}
	choosenPokemon(name) {
		this.setState({
			charOn: true,
			choosedPokemon: name
		});
	}
	goNext() {
		getPokemon(
			json => this.HelloPokemon(json, this.state.Pokemon.currentPage + 1),
			this.state.Pokemon.nextPokemon
		);
	}

	goPrev() {
		getPokemon(
			json => this.HelloPokemon(json, this.state.Pokemon.currentPage - 1),
			this.state.Pokemon.prevPokemon
		);
	}
	goPage(i) {
		let offsett = (i - 1) * 20;
		let url =
			"https://pokeapi.co/api/v2/pokemon?offset=" + offsett + "&limit=20";
		getPokemon(json => this.HelloPokemon(json, i), url);
	}

	componentDidMount() {
		getPokemon(
			json => this.HelloPokemon(json),
			"https://pokeapi.co/api/v2/pokemon"
		);
	}
	render() {
		return (
			<div className="mx-4 md:mx-10 lg:mx-20 py-4 flex flex-col items-center">
				<div>
					<img className="h-20" src="./pokeapi.png" alt="pokeapi" />
				</div>
				{this.state.Pokemon.data ? (
					<PokeList>
						<div>
							<div className="flex justify-between items-center">
								<div>
									<button
										className="btn hidden md:block"
										onClick={() => this.goPrev()}
									>
										← Prev
									</button>
								</div>
								<List
                  pokeList={this.state.Pokemon.data}
                  choosedPokemon={this.state.choosedPokemon.name}
									iChooseYou={pokemon => this.choosenPokemon(pokemon)}
								/>
								<div>
									<button
										className="btn hidden md:block"
										onClick={() => this.goNext()}
									>
										Next →
									</button>
								</div>
							</div>
							
							<div className="flex justify-between items-center">
                <div>
								<button
									className="bg-gray-800 text-white rounded px-2 py-1 border border-white block md:hidden "
									onClick={() => this.goPrev()}
								>
									← 
								</button>
                </div>
								<PageButton
									pages={this.state.Pokemon.pages}
									goPage={i => this.goPage(i)}
									thisPage={this.state.Pokemon.currentPage}
								/>
                <div>
								<button
									className="bg-gray-800 text-white rounded px-2 py-1 border border-white block md:hidden"
									onClick={() => this.goNext()}
								>
									 →
								</button>
                </div>
							</div>
						</div>
					</PokeList>
				) : (
					<h1>Loading </h1>
				)}

			 <CSSTransition 
          in={this.state.charOn}  
          timeout={1100}
          classNames="classnames"
          unmountOnExit={true}>     
				{this.state.choosedPokemon ? (
					<DisplayPokemon choosenOne={this.state.choosedPokemon} />
				) : (
					<h1 className="mt-4 px-4 bg-gray-700 text-white border-2 border-white">
						{" "}
						Gotta Catch'em All!
					</h1>
				)}

				   </CSSTransition>  
			</div>
		);
	}
}

export default Pokemon;
