export const getPokemon = (cb, requestedUrl) => {
  if (!requestedUrl) {
    return null;
  }

  fetch(requestedUrl)
    .then(response => {
      if (!response.ok) {
        throw Error(response.statusText);
      }
      return response.json();
    })
    .then(jsonData => {
      // console.log(jsonData);
      cb(jsonData);
    })
    .catch(error => {
      console.error("getPokemonError:");
      console.error(error);
    });
};

