// Funzione che accetta un array di url in ingresso e restituisce un arrey di json con CB
export const FetchAll = (cb, array) => {
  let DataArray = [];
  //console.log("array di promise: "+ PromiseArray)
  for (let i in array) {
    if (!array[i]) {
      return null;
    }

    fetch(array[i])
      .then(response => {
        if (!response.ok) {
          throw Error(response.statusText);
        }
        //console.log("response di i:" + response.clone().json());
       // PromiseAll.push(response.clone().json());
        return response.json();
        //  return response.json();
      })
      .then(jsonData => {
        const j= parseInt(i)+ 1 ;
        //console.log(jsonData);
       // console.log("giro i=" + i + " j:" +j+ " arr lung:" + array.length);
        DataArray.push(jsonData);
        if ( j === array.length){
           cb(DataArray); 
        }       
      })
      .catch(error => {
        console.error("getPokemonError:");
        console.error(error);
      });
  }
};
