import React from "react";

//import { COLOR } from "./utils.js";

class Stats extends React.Component {
	


	render() {
	
		let array = [];
		let linecolor= "#68d391"
		for (let i in this.props.stats) {
			   let maxStat = 250;
			   let statName= this.props.stats[i].stat.name;
			statName=this.props.stats[i].stat.name === "special-defense"? "Sp Def": (this.props.stats[i].stat.name === "special-attack"? "Sp Atk": this.props.stats[i].stat.name);
			if (this.props.stats[i].stat.name === "hp") {
				linecolor="#4299E1";
				maxStat = 300;
			}
			let stat = (this.props.stats[i].base_stat / maxStat) * 100 + "%";
			const style = {
				backgroundColor:linecolor,
				width: stat
			};

			array.push(
				<li className="flex justify-between" key={i}>
					<div className="mr-4 font-bold capitalize">
						{statName}
					</div>
					<div className="text-left w-40">
						<div className="bg-gray-300 ">
							<div style={style} className="text-white text-sm">
								{this.props.stats[i].base_stat}
							</div>
						</div>
					</div>{" "}
				</li>
			);
		}
		return (
			<div className="text-left text-base ">
				<ul>{array}</ul>
			</div>
		);
	}
}

export default Stats;
