import React from "react";
import Card from "./Card.js";
import { getPokemon } from "./utils.js";

class DisplayPokemon extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: null
    };
  }

  componentDidUpdate(previousProps) {
    if (previousProps.choosenOne !== this.props.choosenOne) {
      this.setState({
        data: null
      });
      getPokemon(json => this.helloPokemon(json), this.props.choosenOne.url);
    }
  }

  helloPokemon(json) {
    this.setState({
      data: json
    });
  }
  componentDidMount() {
    getPokemon(json => this.helloPokemon(json), this.props.choosenOne.url);
  }
  
  render() {
   
    return (
      <div className="w-full md:w-2/3 lg:w-1/2">
    
        {this.state.data?<Card data={this.state.data}/>: <h1 className="mt-4 px-4 bg-gray-700 text-white border-2 border-white"> ...Caricamento...</h1>}
        
      </div>
    );
  }


}

export default DisplayPokemon;
