import React from "react";
import Stats from "./Stats.js";
//import Evolution from "./Evolution.js";
import { getPokemon, FetchAll } from "./utils.js";
import { COLOR } from "./utils.js";

class Card extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: null,
      multyUrl: null
    };
  }
  getFlavour() {
    for (let i in this.state.data.flavor_text_entries) {
      if (this.state.data.flavor_text_entries[i].language.name === "it") {
        return [
          this.state.data.genera[i].genus,
          this.state.data.flavor_text_entries[i].flavor_text
        ];
      }
    }
  }

  pushUrl(data) {
    let moveType;
    if (data) {
      moveType = data.map(element => {
        return element.type.name;
      });
    }
    //console.log(moveType);
    this.setState({
      multyUrl: moveType
    });
  }

  getMultyUrl(Urlarray) {
    let urlMoves = Urlarray.map(array => {
      return array[2];
    });
    FetchAll(data => this.pushUrl(data), urlMoves);
    //console.log("ecco il risultato del multyfetch:"+ results);
   // console.log("LE MOSSE:" + moveType);
  }

  helloPokemon(json) {
    this.setState({
      data: json
    });
  }
  componentDidMount() {
    getPokemon(json => this.helloPokemon(json), this.props.data.species.url);
  }

  // riordino l array in ordine crescente e renderizzo
  orderMove(array) {
    for (let x in array){
      array[x].push(this.state.multyUrl[x]);
    }
   // console.log("array pushato con nomi mosse:"+ array);
	let orderedArray = [];
	
    array.sort((A, B) => {
      if (A[0] > B[0]) {
        return 1;
      }
      if (A[0] < B[0]) {
        return -1;
      }
      return 0;
	});
		
    for (let i in array) {
		//let bgmove= "bg-" + COLOR[this.state.multyUrl[i]];
		let bgmove= "px-1 py-1 rounded bg-" + COLOR[array[i][3]];
      orderedArray.push(
        <div
          key={i}
          className={
            "text-sm text-black font-bold capitalize rounded p-1 mb-2 bg-gray-300 flex justify-between"
          }
        >
          
          <div className="flex items-center w-2/5">
            <div className="w-2/5"> <span className="text-xs text-gray-600 ">lvl</span> {array[i][0]}</div>
            <div className="ml-2 bg-gray-400 rounded p-1 w-4/5">{array[i][1]}</div> 
          </div>
          <div className=" w-1/5 ">  <div className={bgmove}> {array[i][3]}</div></div>
        
        </div>
      );
    }
    return orderedArray;
  }

  render() {
    let flavourText = this.state.data ? this.getFlavour() : "Loading";
    let key = this.props.data.types[0].type.name;
    let cardcolor =
      "py-2 px-3 font-bold relative text-white capitalize bg-" + COLOR[key];
    let idColor =
      "font-bold text-lg text-center text-white p-1 bg-gray-300 bg-" +
      COLOR[key];
    // map per generare le etichette con i tipi di pokemon
    let types = this.props.data.types.map(element => (
      <div
        key={element.type.name}
        className={
          "text-sm text-black font-bold capitalize px-2 mb-2 bg-" +
          COLOR[element.type.name]
        }
      >
        {element.type.name}
      </div>
    ));
    let moves = [];
    let orderedMoves = [];
    for (let i in this.props.data.moves) {
      if (
        this.props.data.moves[i].version_group_details[0].move_learn_method
          .name === "level-up"
      ) {
        moves.push([
          this.props.data.moves[i].version_group_details[0].level_learned_at,
          this.props.data.moves[i].move.name,
          this.props.data.moves[i].move.url
        ]);
      }
    }
	// ENTRY POINT PER FUNZIONE MULTY FETCH
    if (!this.state.multyUrl) {
      this.getMultyUrl(moves);
    }else{ 
	//console.log(urlMoves);	
    orderedMoves = this.orderMove(moves);
}
    return (
      <div className="bg-white flex flex-col mt-6 ">
        <div className={cardcolor}>{this.props.data.name}</div>
        <div>
          <div className="flex items-center justify-around px-5 py-8">
            <div className="mr-10 bg-gray-300">
              <div className={idColor}>#{this.props.data.id}</div>
              <img
                className="pokeimage"
                src={this.props.data.sprites.front_default}
                alt="pokemon"
              />
              <div className="flex justify-around">{types}</div>
            </div>
            <div>
              <div className="font-bold text-lg text-center p-1 mb-2 bg-gray-300">
                {flavourText[0]}
              </div>
              <Stats
                stats={this.props.data.stats}
                data={this.props.data}
                speciesUrl={this.props.data.species.url}
              />
            </div>
          </div>
          <div className="text-base font-normal text-gray-700 px-10 pb-4">
            <div>{flavourText[1]}</div>
          </div>
        </div>
        <div className={cardcolor}> Moves </div>
        <div className="p-5">{orderedMoves}</div>
        {/* <div className={cardcolor}> Evolution </div>
				<div className="p-5">
					<Evolution
						data={this.props.data}
						speciesUrl={this.props.data.species.url}
					/>{" "}
				</div> */}
      </div>
    );
  }
}

export default Card;
