import React from "react";
import classNames from "classnames";

class PageButton extends React.Component {

  render() {
      let array=[];
      for (let i=1; i <= this.props.pages; i++){
          let format = classNames({
            "cursor-pointer hover:bg-gray-800 font-bold p-1 border-white   shadow transition-normal text-xs" :true,
            "bg-blue-800 text-white hover:text-white border": i === this.props.thisPage,
            "bg-red-600 text-white border ": i !== this.props.thisPage
          });
         // console.log("render pagina:" +i );
          array.push(<div className={format} onClick={()=> this.props.goPage(i)} key={i}> {i}</div>)
      }
    return (
      <div className="gridpages mx-20 flex-grow m-4">
        {array}
      </div>
    );
  }
}

export default PageButton;