import React from "react";
import ClassNames from "classnames";

class List extends React.Component {
	render() {
		let arraysx = [];
		let arraydx = [];
		
		let half = this.props.pokeList.length / 2;
		for (let i in this.props.pokeList) {
			let lista = ClassNames({
				"zoom transition-normal flex items-center rounded-tl-lg rounded-bl-lg justify-center rounded border-2 py-1 my-2 hover:bg-blue-700 hover:border-white hover:text-white relative":true,
				"bg-white border-gray-700 text-black": this.props.pokeList[i].name === this.props.choosedPokemon,
				"bg-gray-800 border-white text-white ": this.props.pokeList[i].name !== this.props.choosedPokemon,
			});
			
			if (i < half) {
			
				arraysx.push(
					<li
						className={lista}
						key={i}
						onClick={() => this.props.iChooseYou(this.props.pokeList[i])}
					>
						<div className="pokeball">
							{" "}
							<img className="h-10" src="./pokeball.png" alt="" />
						</div>
						{this.props.pokeList[i].name}
					</li>
				);
			} else {
				arraydx.push(
					<li
          className={lista}
          key={i}
						onClick={() => this.props.iChooseYou(this.props.pokeList[i])}
					>
						<div className="pokeball">
							{" "}
							<img className="h-10" src="./pokeball.png" alt="" />
						</div>

						{this.props.pokeList[i].name}
					</li>
				);
			}
		}
		return (
			<div className=" pokegrid flex-grow text-base font-semibold mx-2 capitalize ">
				<div className="">
					{" "}
					<ul>{arraysx}</ul>
				</div>
				<div className="">
					<ul>{arraydx}</ul>
				</div>
			</div>
		);
	}
}

export default List;
